<template>
  <div class="free-pwd-page">
    <Header>
      <router-link to="/freebanklist" slot="right">支持银行</router-link>
    </Header>
    <div class="free-loading" v-if="loading"> 加载中... </div>
    <div v-else class="card-warp">
      <div class="free-card-list" v-if="cardlists.length">
        <div :class="['card-item',`card-${getBankStyleInfo[item.bankCode] && getBankStyleInfo[item.bankCode].theme}`]" v-for="(item,index) in cardlists" :key="index">
          <div class="card-img" v-if="getBankStyleInfo[item.bankCode]">
            <img :src="`https://cdn09.ehaier.com/shunguang/H5/www/img/${getBankStyleInfo[item.bankCode].img}.png`" alt="">
          </div>
          <div class="other-img" v-else></div>
          <div class="card-body">
            <h3>{{getBankStyleInfo[item.bankCode] && getBankStyleInfo[item.bankCode].name || '其他银行'}} <small>保障中</small></h3>
            <p>{{item.cardNo}}</p>
          </div>
          <div class="card-btns">
            <a href="javascript:;" v-if="item.isDefault">默认卡</a>
            <a href="javascript:;" class="active" v-else @click="setDefault(item)">设为默认卡</a>
            <span @click="cancelBind(item)">解绑</span>
          </div>
        </div>
      </div>
      <div class="free-card-empty" v-else>
        <img src="https://cdn09.ehaier.com/shunguang/H5/www/img/sgmobile/order/card-empty.png" alt="">
        <p>暂无物联支付签约卡</p>
      </div>
    </div>
    <div class="free-add-btn">
      <router-link :to="`/freeapply`">添加物联支付签约信息</router-link>
    </div>
  </div>
</template>

<script>
import Header from '@/components/header'
import URL from '@/http/url'

import { mapGetters } from 'vuex'

export default {
  name: 'freepwdpay',
  components: {
    Header
  },
  data() {
    return {
      ucid: '',
      loading: true,
      cardlists: [],
    }
  },
  computed: {
    ...mapGetters('freebankcard', ['getBankStyleInfo']),
  },
  mounted() {
    this.ucid = this.$route.query.ucid
    // this.originUrl = decodeURIComponent(this.$route.query.originUrl)
    this.fetchData()
  },
  methods: {
    fetchData() {
      this.$http.get(URL.BANKCARD_FREE_LIST).then(({ data }) => {
        let result = data.data;
        console.log(result)
        this.loading = false;
        this.cardlists = result;
      })
    },
    setDefault(item) {
      this.$http.post(URL.SET_FREE_BANK_DEFAULT, {
        id: item.id,
      }).then(({ data }) => {
        let result = data;
        if (result.success) {
          this.$toast('设置成功');
          this.cardlists.forEach(item => { item.isDefault = 0 })
          item.isDefault = 1;
          this.fetchData()
        } else {
          this.$toast(result.messsage)
        }
      })
    },

    cancelBind(item) {
      this.$dialog.confirm({
        message: '确定是否解绑物联支付？',
        confirmButtonText: '确定解绑',
        cancelButtonText: '取消操作',
      }).then(() => {
        this.$http.post(URL.CANCEL_FREE_BANKCORD, {
          id: item.id,
        }).then(({ data }) => {
          let result = data;
          console.log(result, '解绑')
          if (result.success) {
            this.$toast('解绑成功');
            this.cardlists = this.cardlists.filter(card => item.id !== card.id)
            this.fetchData()
          } else {
            this.$toast(result.message)
          }
        })
      })
    }
  }
}
</script>

